* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

.App {
  width: 100vw;
  margin: 0 auto;
  min-height: 100vh;
}

.builder {
  padding: 2rem;
  display: grid;
  place-items: center;
  height: 100%;
}

.cv,
.preview {
  width: 100%;
}

input {
  padding: 0.25rem;
}

button {
  padding: 0.2rem;
}